import { useEffect, useState } from "react";
import {
  RowCustom,
  ButtonCustom,
  RowRouter2,
  RowCustom3,
} from "../ThongTinTiepDan/styled";
import { AndroidOutlined, AppleOutlined } from "@ant-design/icons";
import api from "../../../DanhMuc/DanhMucDanToc/config";
import apiqt from "../../../DanhMuc/DanhMucQuocTich/config";
import apiTHX from "../../../DanhMuc/DMDiaGioi/config";
import Inputnumber from "../../../../../components/uielements/InputNumber";
import Radio from "../../../../../components/uielements/radio";
// import { RowRouter2 } from "../ThongTinTiepDan/styled";
import { Button, Form, Col, Input, Row, Tabs, DatePicker, Select } from "antd";
import { ITEM_LAYOUT, REQUIRED } from "../../../../../settings/constants";
import checkbox from "../../../../../components/uielements/checkbox";
import { set } from "lodash";
const ITEM_LAYOUT_CUSTOM = {
  labelAlign: "left",
  labelCol: { span: 2 },
  wrapperCol: { span: 22 },
};
const DoiTuongKhieuNai = () => {
  const [check, setCheck] = useState(1);
  const [optionsNguoiDaiDien, setOptionNguoiDaiDien] = useState([]);
  const [daidien, setDaiDien] = useState(1);
  const [songuoi, setSoNguoi] = useState(5);
  const [dantoc, setDantoc] = useState();
  const [quoctich, setQuocTich] = useState();
  const [diaGioi, setDiaGioi] = useState({
    tinh: [],
    huyen: [],
    xa: [],
  });

  useEffect(() => {
    api
      .DanhSachDanToc({
        PageNumber: 1,
      })
      .then((res) => setDantoc(res.data.Data));
    apiqt
      .DanhSachQuocTich({ PageNumber: 1 })
      .then((res) => setQuocTich(res.data.Data));
    apiTHX
      .danhSachDiaGioi()
      .then((res) => setDiaGioi({ ...diaGioi, tinh: res.data.Data }));
  }, []);

  useEffect(() => {
    let soNguoiDaiDien = 0;
    let options = [];

    if (songuoi >= 5 && songuoi <= 10) {
      soNguoiDaiDien = 2;
    } else if (songuoi > 10) {
      soNguoiDaiDien = 5;
    } else {
    }

    for (let i = 0; i < soNguoiDaiDien; i++) {
      options.push({
        value: i + 1,
        label: i + 1 + " người",
      });
    }

    setOptionNguoiDaiDien(options);
  }, [songuoi]);
  console.log(daidien);
  const onTinhChange = (value) => {
    apiTHX
      .danhSachDiaGioi({ ID: value, Cap: 2 })
      .then((res) => setDiaGioi({ ...diaGioi, huyen: res.data.Data }));
  };
  const onHuyenChange = (value) => {
    apiTHX
      .danhSachDiaGioi({ ID: value, Cap: 3 })
      .then((res) => setDiaGioi({ ...diaGioi, xa: res.data.Data }));
  };
  const onChange = (e) => {
    setCheck(e.target.value);
  };
  const ViewTapThe = () => {
    let arr = [];
    for (let i = 1; i <= daidien; i++) {
      arr.push(
        <RowCustom3 key={i}>
          <Col span={24}>
            <Form.Item>
              <hr></hr>
            </Form.Item>
          </Col>
          <Col span={24}>
            <Form.Item>
              <span>{`Người đại diện ${i}: `}</span>
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Họ và tên" name={`ten${i}`} rules={[REQUIRED]}>
              <Input />
            </Form.Item>
          </Col>
          <Col span={2}>
            <Form.Item label=" ">
              <ButtonCustom bgcolor={"#fa8c16"}>Kiểm tra trùng</ButtonCustom>
            </Form.Item>
          </Col>
          <Col span={2}>
            <Form.Item label=" ">
              <ButtonCustom bgcolor={"#2878d7"}>Khiếu tố lần 2</ButtonCustom>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Số CCCD/CMND" name={`cmnd${i}`}>
              <Input />
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Ngày cấp" name={`ngaycap${i}`}>
              <DatePicker format={"DD/MM/YYYY"}></DatePicker>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Nơi cấp" name={`noicap${i}`}>
              <Input />
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Giới tính" name={`gioitinh${i}`}>
              <Select>
                <Option value={`nam${i}`}>Nam</Option>
                <Option value={`nu${i}`}>Nữ</Option>
              </Select>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Điện thoại" name={"dienthoai"}>
              <Input />
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Dân tộc" name={`dantoc${i}`}>
              <Select>
                {dantoc?.map((item) => (
                  <Option value={item.DanTocID}>{item.TenDanToc}</Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item
              label="Quốc tịch"
              name={`quoctich${i}`}
              rules={[REQUIRED]}
            >
              <Select>
                {quoctich?.map((item) => (
                  <Option value={item.QuocTichID}>{item.TenQuocTich}</Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Nghề nghiệp" name={`nghenghiep${i}`}>
              <Input />
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item
              label={
                <span>
                  Địa chỉ (
                  <span style={{ color: "#D01E1E" }}>Chọn tỉnh,huyện,xã</span>)
                </span>
              }
              name={`tinh${i}`}
            >
              <Select onChange={onTinhChange} placeholder="Chọn tỉnh">
                {diaGioi.tinh.map((item, index) => (
                  <Option key={index} value={item.ID}>
                    {item.Ten}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label={" "} name={`huyen${i}`}>
              <Select onChange={onHuyenChange} placeholder="Chọn huyện">
                {diaGioi.huyen.map((item, index) => (
                  <Option key={index} value={item.ID}>
                    {item.Ten}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label={" "} name={`xa${i}`}>
              <Select placeholder="Chọn xã">
                {diaGioi.xa.map((item, index) => (
                  <Option key={index} value={item.ID}>
                    {item.Ten}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Nhập số nhà, tổ, thôn xóm..." name={`sonha${i}`}>
              <Input />
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Chi tiết địa chỉ" name={`diachititiet${i}`}>
              <Input />
            </Form.Item>
          </Col>
        </RowCustom3>
      );
    }
    return arr;
  };
  const ChangeSoNguoi = (value) => {
    setSoNguoi(value ? value : 5);
  };

  const onChangeNguoiDaiDien = (value) => {
    setDaiDien(value);
  };

  console.log(daidien, "So Nguoi dai dien");

  return (
    <RowCustom gutter={24}>
      <Col span={24}>
        <Form.Item
          label={<span>Loại đối tượng:</span>}
          name="loaidoituong"
          {...ITEM_LAYOUT_CUSTOM}
        >
          <Radio.Group onChange={onChange} value={check} defaultValue={1}>
            <Radio value={1}>Cá nhân</Radio>
            <Radio value={2}>Cơ quan, tổ chức</Radio>
            <Radio value={3}>Tập thể</Radio>
          </Radio.Group>
        </Form.Item>
        <Form.Item>
          <hr></hr>
        </Form.Item>
      </Col>
      {check == 2 ? (
        <div className="router2">
          <Col span={24}>
            <span className="title-coquan">Thông tin cơ quan, tổ chức:</span>
          </Col>
          <RowRouter2 gutter={24} style={{ margin: "auto" }}>
            <Col span={8}>
              <Form.Item
                label="Tên cơ quan, tổ chức"
                name={"tencoquan"}
                rules={[REQUIRED]}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col span={16}>
              <Form.Item
                label="Địa chỉ cơ quan, tổ chức"
                name={"diachicoquan"}
                rules={[REQUIRED]}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col span={24}>
              <Form.Item>
                <hr></hr>
              </Form.Item>
            </Col>
            <Col span={24}>
              <Form.Item
                label={<span>Người đại diện:</span>}
                name="nguoidaidien"
                {...ITEM_LAYOUT_CUSTOM}
              >
                <Radio.Group defaultValue={1}>
                  <Radio value={true}>Đại diện phát lý</Radio>
                  <Radio value={false}>Được ủy quyền</Radio>
                </Radio.Group>
              </Form.Item>
            </Col>
          </RowRouter2>
        </div>
      ) : (
        ""
      )}

      {check == 1 || check == 2 ? (
        <>
          <Col span={8}>
            <Form.Item label="Họ và tên" name={"ten"} rules={[REQUIRED]}>
              <Input />
            </Form.Item>
          </Col>
          <Col span={2}>
            <Form.Item label=" ">
              <ButtonCustom bgcolor={"#fa8c16"}>Kiểm tra trùng</ButtonCustom>
            </Form.Item>
          </Col>
          <Col span={2}>
            <Form.Item label=" ">
              <ButtonCustom bgcolor={"#2878d7"}>Khiếu tố lần 2</ButtonCustom>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Số CCCD/CMND" name={"cmnd"}>
              <Input />
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Ngày cấp" name={"ngaycap"}>
              <DatePicker format={"DD/MM/YYYY"}></DatePicker>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Nơi cấp" name={"noicap"}>
              <Input />
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Giới tính" name={"goitinh"}>
              <Select>
                <Option value="nam">Nam</Option>
                <Option value="nu">Nữ</Option>
              </Select>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Điện thoại" name={"dienthoai"}>
              <Input />
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Dân tộc" name={"dantoc"}>
              <Select>
                {dantoc?.map((item) => (
                  <Option value={item.DanTocID}>{item.TenDanToc}</Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Quốc tịch" name={"quoctich"}>
              <Select>
                {quoctich?.map((item) => (
                  <Option value={item.QuocTichID}>{item.TenQuocTich}</Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Nghề nghiệp" name={"nghenghiep"}>
              <Input />
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item
              label={
                <span>
                  Địa chỉ (
                  <span style={{ color: "#D01E1E" }}>Chọn tỉnh,huyện,xã</span>)
                </span>
              }
              name={"tinh"}
            >
              <Select onChange={onTinhChange} placeholder="Chọn tỉnh">
                {diaGioi.tinh.map((item, index) => (
                  <Option key={index} value={item.ID}>
                    {item.Ten}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label={" "} name={"huyen"}>
              <Select onChange={onHuyenChange} placeholder="Chọn huyện">
                {diaGioi.huyen.map((item, index) => (
                  <Option key={index} value={item.ID}>
                    {item.Ten}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label={" "} name={"xa"}>
              <Select placeholder="Chọn xã">
                {diaGioi.xa.map((item, index) => (
                  <Option key={index} value={item.ID}>
                    {item.Ten}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Nhập số nhà, tổ, thôn xóm..." name={"sonha"}>
              <Input />
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Chi tiết địa chỉ" name={"chitietdiachi"}>
              <Input />
            </Form.Item>
          </Col>
        </>
      ) : (
        ""
      )}
      {check == 3 ? (
        <div className="router2">
          <Col span={24}>
            <span className="title-coquan">Thông tin tập thể:</span>
          </Col>
          <RowRouter2 gutter={24} style={{ margin: "auto" }}>
            <Col span={4}>
              <Form.Item label="Số người" name={"songuoi"} rules={[REQUIRED]}>
                <Inputnumber
                  min={5}
                  onChange={ChangeSoNguoi}
                  defaultValue={5}
                />
              </Form.Item>
            </Col>
            <Col span={4}>
              <Form.Item label="Số người đại diện" name={"songuoidaidien"}>
                <Select
                  defaultValue={1}
                  options={optionsNguoiDaiDien}
                  onChange={onChangeNguoiDaiDien}
                ></Select>
              </Form.Item>
            </Col>
          </RowRouter2>
          {ViewTapThe().map((item) => item)}
        </div>
      ) : (
        <></>
      )}
    </RowCustom>
  );
};

export default DoiTuongKhieuNai;
