import {
  Modal,
  Table,
  Tooltip,
  message,
  Row,
  Col,
  Input,
  Collapse,
  Form,
} from "antd";
import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import actions from "../../../redux/DanhMuc/DanhMucDanToc/action";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import PageAction from "../../../../components/utility/pageAction";
import Box from "../../../../components/utility/box";
import BoxFilter from "../../../../components/utility/boxFilter";
import BoxTable from "../../../../components/utility/boxTable";
import Checkbox from "../../../../components/uielements/checkbox";
import dayjs from "dayjs";
import {
  Button,
  DatePicker,
  InputSearch,
} from "../../../../components/uielements/exportComponent";
import {
  changeUrlFilter,
  getDefaultPageSize,
  getFilterData,
  getRoleByKey,
} from "../../../../helpers/utility";
import { useKey } from "../../../CustomHook/useKey";
import queryString from "query-string";
import api from "./config";
import { DeleteOutlined, EditOutlined, PlusOutlined } from "@ant-design/icons";
import { CheckboxGroup } from "../../../../components/uielements/checkbox";
import CustomCollapseStyled from "./customCollapse";
import Wrapper, {
  FooterPageAction,
  ButtonSave,
  ButtonCancel,
  ButtonCancelPrimary,
  ButtonSavePrint,
  ButtonCancelForm,
} from "./styled";
import TTHSTiepDan from "./ThongTinTiepDan/thongtintiepdan";
import DoiTuongKhieuNai from "./DoiTuongKhieuNai/DoiTuongKhieuNai";
import PhanLoai from "./PhanLoaiVuViec/PhanLoai";
const { Item, useForm } = Form;

const QLNamHoc = (props) => {
  const [form] = useForm();
  const { Panel } = Collapse;
  document.title = "Tiếp Dân Trực Tiếp";
  const [filterData, setFilterData] = useState(
    queryString.parse(props.location.search)
  );
  const [dataModalAddEdit, setDataModalAddEdit] = useState({});
  const [visibleModalAddEdit, setVisibleModalAddEdit] = useState(false);
  const [action, setAction] = useState("");
  const [modalKey, inceaseModalKey] = useKey();
  const [selectedRowsKey, setSelectedRowsKey] = useState([]);
  const [confirmLoading, setConfirmLoading] = useState(false);

  useEffect(() => {
    changeUrlFilter(filterData);
    props.getData(filterData);
  }, [filterData]);

  useEffect(() => {
    props.getData(filterData);
  }, []);

  const onTableChange = (pagination, filters, sorter) => {
    let oldFilterData = filterData;
    let onOrder = { pagination, filters, sorter };
    let newFilterData = getFilterData(oldFilterData, null, onOrder);

    setFilterData(newFilterData);
    setSelectedRowsKey([]);
  };

  const onFilter = (value, property) => {
    let oldFilterData = filterData;
    let onFilter = { value, property };
    let newfilterData = getFilterData(oldFilterData, onFilter, null);
    //get filter data
    setFilterData(newfilterData);
    setSelectedRowsKey([]);
  };
  const saveData = async () => {
    const value = await form.validateFields();
    console.log(value);
  };
  const resetForm = async () => {
    await form.resetFields();
  };

  return (
    <Wrapper>
      <LayoutWrapper>
        <PageHeader>Tiếp dân trực tiếp</PageHeader>
        <PageAction>
          {/* {role ? role.add ?  */}
          <ButtonSave type="primary">Lưu</ButtonSave>
          <ButtonSavePrint type="primary">Lưu và in</ButtonSavePrint>
          {/* //  : '' : ''} */}
          <ButtonCancelPrimary type="primary">
            Hủy, nhập đơn mới
          </ButtonCancelPrimary>
          <ButtonCancelForm type="primary" bgcolor="#cf1322">
            Dân không đến
          </ButtonCancelForm>
        </PageAction>
        <Box>
          {/* Content */}
          <Form
            form={form}
            name={"tiep dan truc tiep"}
            layout="vertical"
            // labelCol={{
            //   span: 24,
            // }}
            // wrapperCol={{
            //   span: 24,
            // }}
            initialValues={{
              vuvieccu: true,
              ngaytiep: dayjs(),
              ngaycap: dayjs(),
              ngaycap1: dayjs(),
              ngaycap2: dayjs(),
              ngaycap3: dayjs(),
              ngaycap4: dayjs(),
              ngaycap5: dayjs(),
              loaidoituong: 1,
            }}
          >
            <CustomCollapseStyled>
              <Panel header={"Thông tin hồ sơ tiếp dân"} key="1">
                <TTHSTiepDan />
              </Panel>
              <Panel header={"Đối tượng khiếu nại, tố cáo"} key="2">
                <DoiTuongKhieuNai />
              </Panel>
              <Panel header="Phân loại vụ việc" key="3">
                <PhanLoai />
              </Panel>
            </CustomCollapseStyled>
          </Form>
        </Box>
        <FooterPageAction>
          {/* {role ? role.add ?  */}
          <ButtonSave
            type="primary"
            bgcolor="#fff"
            color="#fa8c16"
            onClick={saveData}
          >
            Lưu
          </ButtonSave>
          <ButtonSavePrint type="primary" bgcolor="#fff" color="#fa8c16">
            Lưu và in
          </ButtonSavePrint>
          {/* //  : '' : ''} */}
          <ButtonCancel type="primary" onClick={resetForm}>
            Hủy, nhập đơn mới
          </ButtonCancel>
          <ButtonCancelForm type="primary" bgcolor="#cf1322">
            Dân không đến
          </ButtonCancelForm>
        </FooterPageAction>
      </LayoutWrapper>
    </Wrapper>
  );
};

function mapStateToProps(state) {
  return {
    ...state.ReducerDanToc,
    role: getRoleByKey(state.Auth.role, "quan-ly-nam-hoc"),
  };
}

export default connect(mapStateToProps, actions)(QLNamHoc);
